const StatusTestPage = ({ pageContext }) => {
  const [testResult, setTestResult] = useState(null);
  const [testStarted, setTestStarted] = useState(false);
  const status = pageContext?.status || "search";

  const renderTest = () => {
    switch (status) {
      case "relationship":
        return <RelationshipTest onComplete={setTestResult} />;
      case "breakup":
        return <BreakupTest onComplete={setTestResult} />;
      default:
        return <SearchTest onComplete={setTestResult} />;
    }
  };

  const getTestTitle = () => {
    switch (status) {
      case "relationship":
        return "Тест: Оценка ваших отношений";
      case "breakup":
        return "Тест: Восстановление после расставания";
      default:
        return "Тест: Готовность к отношениям";
    }
  };

  const getTestIcon = () => {
    switch (status) {
      case "relationship":
        return (
          <RelationshipIcon
            style={{ fontSize: 50, color: "#6c5ce7", marginBottom: 20 }}
          />
        );
      case "breakup":
        return (
          <BreakupIcon
            style={{ fontSize: 50, color: "#6c5ce7", marginBottom: 20 }}
          />
        );
      default:
        return (
          <SearchIcon
            style={{ fontSize: 50, color: "#6c5ce7", marginBottom: 20 }}
          />
        );
    }
  };

  return (
    <Layout>
      <Container>
        {getTestIcon()}
        <Title>{getTestTitle()}</Title>

        {!testStarted ? (
          <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
            <p style={{ marginBottom: 30, fontSize: "1.1rem" }}>
              Ответьте на несколько вопросов, чтобы получить персональные
              рекомендации
            </p>
            <SubmitButton
              onClick={() => setTestStarted(true)}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              Начать тест
            </SubmitButton>
          </motion.div>
        ) : testResult ? (
          <ResultCard
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
          >
            <h3>Ваш результат</h3>
            <div>
              <p>
                <strong>{testResult.summary}</strong>
              </p>

              {testResult.attachmentType && (
                <div style={{ marginTop: 15 }}>
                  <h4>Тип привязанности:</h4>
                  <p>{testResult.attachmentType}</p>
                  {testResult.psychologicalProfile && (
                    <p>
                      <em>{testResult.psychologicalProfile}</em>
                    </p>
                  )}
                </div>
              )}

              {testResult.strengths?.length > 0 && (
                <div style={{ marginTop: 15 }}>
                  <h4>Сильные стороны:</h4>
                  <ul>
                    {testResult.strengths.map((item, i) => (
                      <li key={i}>{item}</li>
                    ))}
                  </ul>
                </div>
              )}

              {testResult.challenges?.length > 0 && (
                <div style={{ marginTop: 15 }}>
                  <h4>Зоны для развития:</h4>
                  <ul>
                    {testResult.challenges.map((item, i) => (
                      <li key={i}>{item}</li>
                    ))}
                  </ul>
                </div>
              )}

              {testResult.recommendations?.length > 0 && (
                <div style={{ marginTop: 15 }}>
                  <h4>Рекомендации:</h4>
                  <ul>
                    {testResult.recommendations.map((item, i) => (
                      <li key={i}>{item}</li>
                    ))}
                  </ul>
                </div>
              )}

              {testResult.specificExercises?.length > 0 && (
                <div style={{ marginTop: 15 }}>
                  <h4>Практические упражнения:</h4>
                  {testResult.specificExercises.map((group, i) => (
                    <div key={i} style={{ marginBottom: 15 }}>
                      <h5>{group.category}:</h5>
                      <ul>
                        {group.activities?.map((activity, j) => (
                          <li key={j}>
                            <strong>{activity.title}</strong> -{" "}
                            {activity.description} ({activity.duration})
                          </li>
                        ))}
                      </ul>
                    </div>
                  ))}
                </div>
              )}

              {testResult.books?.length > 0 && (
                <div style={{ marginTop: 15 }}>
                  <h4>Рекомендуемые книги:</h4>
                  <ul>
                    {testResult.books.map((book, i) => (
                      <li key={i}>
                        "{book.title}" - {book.author}{" "}
                        {book.focus && `(${book.focus})`}
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>

            <SubmitButton
              as={Link}
              to="/status"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              style={{ background: "#00cec9", marginTop: 20 }}
            >
              Вернуться к выбору
            </SubmitButton>
          </ResultCard>
        ) : (
          renderTest()
        )}
      </Container>
    </Layout>
  );
};

export default StatusTestPage;
